/* =============
    Partners
============= */

.partners-grid{
    display: grid;
    grid-template-columns: 100%;

    @mixin md{
        grid-template-columns: 50% 50%;
    }

    @mixin lg{
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-areas: 
        "our-story our-story shop"
        "flora . shop"
        "flora cafe cafe";
    }

    .our-story-container{
        @mixin lg{
            grid-area: our-story;
    }
    }

    .shop-container{
        @mixin lg{
            grid-area: shop-story;
        }
        }

    .flora-container{
        @mixin lg{
            grid-area: flora-story;
        }
        }

    .cafe-container{
        @mixin lg{
            grid-area: cafe-story;
        }
        }
    }
